.audi-content-center {
width: calc(100% - 50%);
height: calc(100% - 50%);
z-index: 1;
position: absolute;
top: 12%;
left: -5.3%;
right: 0;
margin: 0 auto;
  }

.background-fluid {
    width: 100%;
    height: auto;
    max-width: 100%;
}

.audi-content {
    height: 100%;
    width: 100%;
}

.audi-background {
    position: relative;
    width: 100%;
    height: 100%;
    display: block;
}